/* =========================
   RIGHT (Telegram-like)
   ========================= */

.msg__right{
  border: 1px solid rgba(0,0,0,.12);
  background: rgba(255,255,255,.7);
  display: flex;
  flex-direction: column;
  min-height: 0;
}

/* topbar */
 .msg__topbar{
  flex: 0 0 auto;
  padding: 12px 14px;
  border-bottom: 1px solid rgba(0,0,0,.08);
  background: rgba(255,255,255,.85);
}

 .msg__chatTitle{
  font-weight: 800;
  font-size: 16px;
}

/* chat layout: messages (scroll) + composer (bottom) */
 .msg__chat{
  flex: 1;
  min-height: 0;
  display: flex;
  flex-direction: column;
}

/* =========================
   MESSAGES AREA
   ========================= */

.msg__messages{
  flex: 1;
  min-height: 0;
  overflow-y: auto;
  padding: 16px 16px 10px;
  display: flex;
  flex-direction: column;
  gap: 10px; /* –ù–ï —Å–ª–∏–ø–∞–ª–∏—Å—å */
}


 .bubbleRow{
  display: flex;
  width: 100%;
}

.bubbleRow--mine{ justify-content: flex-end; }
 .bubbleRow--their{ justify-content: flex-start; }

/* bubble itself */
 .bubble{
  width: fit-content;               /* –≤–∞–∂–Ω–æ–µ: —à–∏—Ä–∏–Ω–∞ –ø–æ –∫–æ–Ω—Ç–µ–Ω—Ç—É */
  max-width: min(520px, 78%);       /* —á—Ç–æ–±—ã –Ω–µ –±—ã–ª–æ –æ–¥–∏–Ω–∞–∫–æ–≤—ã—Ö/–Ω–∞ –≤—Å—é —à–∏—Ä–∏–Ω—É */
  padding: 10px 12px 8px;
  border-radius: 16px;
  line-height: 1.35;
  font-size: 14px;
  box-shadow: 0 1px 0 rgba(0,0,0,.06);
  border: 1px solid rgba(0,0,0,.06);
  background: #fff;
  white-space: prep-wrap;
  word-wrap: break-word;
  overflow-wrap: anywhere;
  word-break: break-word;
  overflow-x: hidden;
}

.bubble--their{
  background: #ffffff;
  color: #111;
  border-top-left-radius: 8px;
}

.bubble--mine{
  background: #d9f0ff;
  color: #0b1a2a;
  border-top-right-radius: 8px;
}

 .bubble__text{
  white-space: pre-wrap;
  word-break: break-word;
}

 .bubble__meta{
  margin-top: 6px;
  font-size: 11px;
  opacity: .6;
  text-align: right;
}

/* –µ—Å–ª–∏ —Ç—ã –ù–ï –¥–æ–±–∞–≤–ª—è–ª–∞ bubbleRow, –∞ –ø—É–∑—ã—Ä–∏ —Å—Ç–æ—è—Ç –ø—Ä—è–º–æ –≤ msg__messages,
   —Ç–æ–≥–¥–∞ –æ—Å—Ç–∞–≤—å align-self, —á—Ç–æ–±—ã –≤—ã—Ä–∞–≤–Ω–∏–≤–∞–ª–∏—Å—å */
.msg__messages > .bubble--mine{ align-self: flex-end; }
 .msg__messages > .bubble--their{ align-self: flex-start; }

/* =========================
   COMPOSER pinned bottom
   ========================= */

.msg__composer{
  flex: 0 0 auto;
  display: flex;
  gap: 10px;
  padding: 12px 16px;
  border-top: 1px solid rgba(0,0,0,.08);
  background: rgba(255,255,255,.95);
  position: sticky;
  bottom: 0;
}

 .msg__input{
  flex: 1;
  height: 44px;
  padding: 0 14px;
  border: 1px solid rgba(0,0,0,.12);
  outline: none;
  background: #fff;
  border-radius: 12px;
}

 .msg__send{
  height: 44px;
  padding: 0 16px;
  border: 1px solid rgba(0,0,0,.16);
  background: rgba(255,255,255,.95);
  font-weight: 700;
  cursor: pointer;
  border-radius: 12px;
}

 .msg__send:disabled{
  opacity: .55;
  cursor: not-allowed;
}

/* LEFT like Telegram */

.msg__left{
  border: none;
  border-right: 1px solid rgba(0,0,0,.10); /* —Ä–∞–∑–¥–µ–ª–∏—Ç–µ–ª—å –∫–∞–∫ –≤ TG */
  background: #ffffff;
  display: flex;
  flex-direction: column;
  min-height: 0;
}

 .msg__leftHead{
  padding: 10px 12px 12px;
  border-bottom: 1px solid rgba(0,0,0,.08);
  background: rgba(255,255,255,.95);
}

 .msg__leftTop{
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  margin-bottom: 10px;
}

 .msg__title{
  font-weight: 800;
  font-size: 18px; /* —á—É—Ç—å –∫—Ä—É–ø–Ω–µ–µ */
}

 .msg__search{
  margin-top: 10px;
}
.msg__searchInput{
  width: 100%;
  height: 40px;
  border-radius: 999px;
  border: 1px solid rgba(0,0,0,.10);
  background: rgba(0,0,0,.04);
  padding: 0 14px;
  outline: none;
}
.msg .msg__searchInput:focus{
  background: #fff;
  border-color: rgba(0,0,0,.18);
}

 .msgList{
  flex: 1;
  overflow-y: auto;
  padding: 6px;
}

 .msgItem{
  width: 100%;
  display: grid;
  grid-template-columns: 44px 1fr;
  gap: 10px;
  padding: 10px 10px;
  border-radius: 12px;
  border: 1px solid transparent;
  background: transparent;
  cursor: pointer;
  text-align: left;
  transition: background .15s, border-color .15s;
}

 .msgItem:hover{
  background: rgba(0,0,0,.04);
}

 .msgItem.isActive{
  background: rgba(79,124,255,.18);
  border-color: rgba(79,124,255,.22);
}

.msgItem__avatar{
  width: 44px;
  height: 44px;
  border-radius: 50%;
  overflow: hidden;
  background: rgba(0,0,0,.06);
  display: grid;
  place-items: center;
}

 .msgItem__avatarImg{
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

 .msgItem__body{
  min-width: 0;
  display: grid;
  gap: 4px;
}

.msgItem__top{
  display: flex;
  align-items: center;
  gap: 8px;
}

 .msgItem__name{
  font-weight: 800;
  font-size: 14px;
  min-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

 .msgItem__time{
  margin-left: auto;
  font-size: 12px;
  opacity: .6;
  white-space: nowrap;
}

.msgItem__bottom{
  display: flex;
  align-items: center;
  gap: 10px;
}

 .msgItem__preview{
  min-width: 0;
  font-size: 12px;
  opacity: .7;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

 .msgItem__badge{
  margin-left: auto;
  min-width: 18px;
  height: 18px;
  padding: 0 6px;
  border-radius: 999px;
  background: rgba(79,124,255,.35);
  border: 1px solid rgba(79,124,255,.45);
  font-size: 12px;
  font-weight: 800;
  display: grid;
  place-items: center;
}

.msg{
 height: 100vh;            
  display: grid;
  grid-template-columns: 420px 1fr;  
  gap: 0;                   
  padding: 0;             
    background: linear-gradient(
    180deg,
    #f3efff 0%,
    #ede8ff 35%,
    #e6deff 65%,
    #e0d6ff 100%
  );     
  gap: 0;
}
